/**
 * Clamps value `x` to given closed interval.
 *
 * @param x value to clamp
 * @param min lower bound
 * @param max upper bound
 */
export var clamp = function clamp(x, min, max) {
  return x < min ? min : x > max ? max : x;
};
export var clamp01 = function clamp01(x) {
  return x < 0 ? 0 : x > 1 ? 1 : x;
};
export var clamp11 = function clamp11(x) {
  return x < -1 ? -1 : x > 1 ? 1 : x;
};
export var wrap = function wrap(x, min, max) {
  return x < min ? x - min + max : x >= max ? x - max + min : x;
};
export var wrap01 = function wrap01(x) {
  return x < 0 ? x + 1 : x >= 1 ? x - 1 : x;
};
export var wrap11 = function wrap11(x) {
  return x < -1 ? x + 2 : x >= 1 ? x - 2 : x;
};
export var min2id = function min2id(a, b) {
  return a <= b ? 0 : 1;
};
export var min3id = function min3id(a, b, c) {
  return a <= b ? a <= c ? 0 : 2 : b <= c ? 1 : 2;
};
export var min4id = function min4id(a, b, c, d) {
  return a <= b ? a <= c ? a <= d ? 0 : 3 : c <= d ? 2 : 3 : b <= c ? b <= d ? 1 : 3 : c <= d ? 2 : 3;
};
export var max2id = function max2id(a, b) {
  return a >= b ? 0 : 1;
};
export var max3id = function max3id(a, b, c) {
  return a >= b ? a >= c ? 0 : 2 : b >= c ? 1 : 2;
};
export var max4id = function max4id(a, b, c, d) {
  return a >= b ? a >= c ? a >= d ? 0 : 3 : c >= d ? 2 : 3 : b >= c ? b >= d ? 1 : 3 : c >= d ? 2 : 3;
};
/**
 * See `smax()`.
 *
 * @param a
 * @param b
 * @param k smooth exponent (MUST be > 0)
 */

export var smin = function smin(a, b, k) {
  return smax(a, b, -k);
};
/**
 * Smooth maximum. Note: Result values will be slightly larger than max
 * value near max(a,b) + eps due to exponential decay. Higher `k` values
 * reduce the error, but also reduce the smoothing. Recommended k=16.
 *
 * https://en.wikipedia.org/wiki/Smooth_maximum
 *
 * @param a
 * @param b
 * @param k smooth exponent (MUST be > 0)
 */

export var smax = function smax(a, b, k) {
  var ea = Math.exp(a * k);
  var eb = Math.exp(b * k);
  return (a * ea + b * eb) / (ea + eb);
};
/**
 * Same as `smin(smax(x, min, k), max, k)`.
 *
 * @param x
 * @param min
 * @param max
 * @param k
 */

export var sclamp = function sclamp(x, min, max, k) {
  return smin(smax(x, min, k), max, k);
};
export var absMin = function absMin(a, b) {
  return Math.abs(a) < Math.abs(b) ? a : b;
};
export var absMax = function absMax(a, b) {
  return Math.abs(a) > Math.abs(b) ? a : b;
};
/**
 * http://www.musicdsp.org/showone.php?id=203
 *
 * @param e
 * @param x
 */

export var foldback = function foldback(e, x) {
  return x < -e || x > e ? Math.abs(Math.abs((x - e) % (4 * e)) - 2 * e) - e : x;
};
/**
 * Returns true iff `x` is in closed interval `[min .. max]`
 *
 * @param x
 * @param min
 * @param max
 */

export var inRange = function inRange(x, min, max) {
  return x >= min && x <= max;
};
/**
 * Returns true iff `x` is in open interval `(min .. max)`
 *
 * @param x
 * @param min
 * @param max
 */

export var inOpenRange = function inOpenRange(x, min, max) {
  return x > min && x < max;
};